.card--teaser {
    .uk-padding-small {
        padding: 20px;
    }
}

.buka-cal-wrapper,
.minikalender {
    font-weight: normal;
    display: flex;
    flex-flow: wrap;
    justify-content: space-between;
    margin-top: 10px;
    .y-wrapper {
        align-content: flex-start;
        border: 1px solid #999999;
        display: flex;
        flex-flow: wrap;
        list-style: none;
        width: calc(100% - 20px);
        margin-top: 0;
        padding: 2px;
        li {
            text-align: center;
            width: 14.2857%;
        }
        .bk_month_title {
            width: 100%;
        }
        .bk-day {
            font-weight: normal;
        }
        @media screen and (min-width: 540px) {
            width: calc(50% - 20px);
        }
        @media screen and (min-width: 840px) {
            width: calc(33.3333% - 20px);
        }
        @media screen and (min-width: 1600px) {
            width: calc(25% - 20px);
        }
        @media screen and (min-width: 2048px) {
            width: calc(25% - 20px);
        }
    }
}

.text-container {
    a {
        font-weight: bold;
        font-size: 90%;
        transition: border-color 0.5s;
        border-color: transparent;
        &:hover {
            border-bottom: 2px solid $global-headline-color;
        }
    }
    blockquote {
        background-color: $global-muted-background;
        font-size: 1em;
        padding: $global-margin;
    }
}

.minikalender-wrapper {
    .buka_pager_nav {
        a {
            font-weight: bold;
            color: $global-headline-color;
            transition: border-color 0.5s;
            border-color: transparent;
            &:hover {
                border-bottom: 2px solid $global-headline-color;
            }
        }
    }
}

.minikalender {
    .y-wrapper {
        @media screen and (min-width: 540px) {
            width: calc(50% - 20px);
        }
        @media screen and (min-width: 840px) {
            width: calc(33.3333% - 20px);
        }
        @media screen and (min-width: 1600px) {
            width: calc(16.6% - 15px);
        }
        @media screen and (min-width: 2048px) {
            width: calc(16.6% - 15px);
        }
    }
    font-size: 0.7rem;
    .bk-day {
        cursor: inherit;
    }
}

.bildunterschrift--imagewide {
    @media screen and (min-width: 1280px) {
        padding: 0 20px;
    }
}

.uk-logo {
    img {
        height: auto;
        width: 200px;
    }
}

.mobile-logo {
    display: inline-block;
    padding-top: 10px;
    margin-left: 20px
}

footer {
    .footer-logo {
        margin-top: 6px;
        width: 200px;
    }
}

.uk-section-primary {
    a {
        color: #ffffff;
    }
}

input, form button {
    height: 60px !important;
}

form {
    .uk-button {
        line-height: 60px;
    }
}