.mainnav {
    > ul {
        > li:first-child {
            @media screen and (max-width: 1400px) {
                display: none;
            }
        }
    }
}

.uk-navbar-transparent {
    ul {
        li {
            a {
                color: $global-headline-color;
                &:hover {
                    background-color: rgba(255, 255, 255, 0.2);
                    //                    color: #ffffff;
                }
            }
            &.uk-active {
                > a {
                    color: $global-headline-color;
                    font-weight: bold;
                }
            }
        }
        ul {
            li {
                a {
                    white-space: nowrap;
                }
            }
        }
    }
}

.uk-navbar-dropdown {
    background-color: $global-bright-background;
    min-width: 200px;
    width: auto;
}

.container--metanav {
    ul.uk-navbar-nav {
        li {
            a {
                color: #ffffff;
                font-size: $global-small-font-size * 0.8;
//                font-weight: bold;
                min-height: 0;
                padding-top: 5px;
                padding-bottom: 5px;
            }
        }
    }
}

.footer--footernav {
    li {
        a {
            display: block;
            color: #ffffff;
            &:hover {
                text-decoration: underline;
            }
        }
    }
    > li {
        > a {
            font-weight: bold;
        }
    }
}

.uk-navbar-toggle {
    margin-bottom: 0;
    span {
        margin-top: 20px;
    }
}

.mobilenav {
    a.uk-current {
        background-color: $global-muted-background;
        font-weight: bold;
    }
    ul.lev-0 {
        > li {
            margin-bottom: 20px;
            > a {
                font-weight: bold;
            }
        }
    }
}