/*
h1, h2, h3, h4, h5, h6,
.uk-h1, .uk-h2, .uk-h3, .uk-h4, .uk-h5, .uk-h6 {
    font-family: "Vollkorn", "Times", serif;
    color: $global-headline-color;
    font-style: italic;
}
*/
h1, h2, h3, h4, h5, h6,
.uk-h1, .uk-h2, .uk-h3, .uk-h4, .uk-h5, .uk-h6 {
    color: $global-headline-color;
    font-weight: 400;
    font-style: normal;
    line-height: 1;
}


.uk-h1 { font-size: 2.8rem; }
.uk-h2 { font-size: 2.4rem; }
.uk-h3 { font-size: 2rem; }
.uk-h4 { font-size: 1.8rem; }
.uk-h5 { font-size: 1.6rem; }

.text-weiss {
    color: #ffffff;
}

@media screen and (max-width: 550px) {
    .uk-h1 { font-size: 11.2vw }
    body {
        font-size: 19px;
    }
}